import React from "react";
import styled, { keyframes, createGlobalStyle } from "styled-components";
import smkawal from "../../assets/smkawal.jpg";

//  Google Fonts CDN (pasti jalan)
const GoogleFonts = createGlobalStyle`
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600;700;800&display=swap');
`;

const fadeInUp = keyframes`
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const Section = styled.section`
  min-height: 100vh;
  width: 100vw;
  position: relative;
  background: url(${smkawal}) no-repeat center center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 50, 0, 0.5) 100%);
    z-index: 1;
  }

  @media (max-width: 768px) {
    padding: 2rem;
  }
`;

const Content = styled.div`
  position: relative;
  z-index: 2;
  max-width: 900px;
  padding: 2rem;
  color: #fff;
`;

const MainTitle = styled.h1`
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  font-size: 4.5rem;
  color: #fff;
  margin: 0 0 0.5rem 0;
  letter-spacing: 1.5px;
  line-height: 1.1;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.4);
  animation: ${fadeInUp} 1.2s ease forwards;

  @media (max-width: 768px) {
    font-size: 3rem;
  }
`;

const Subtitle = styled.h2`
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 3rem;
  color: #a8d672;
  margin: 0 0 2.5rem 0;
  letter-spacing: 1px;
  text-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);
  animation: ${fadeInUp} 1.5s ease forwards;

  @media (max-width: 768px) {
    font-size: 2.2rem;
    margin: 0 0 2rem 0;
  }
`;

const CTAButton = styled.a`
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 1.3rem;
  color: #fff;
  background: #2e7d32; /* Hijau tua (Forest Green) */
  padding: 1rem 2.8rem;
  border-radius: 35px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 6px 20px rgba(46, 125, 50, 0.35);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  animation: ${fadeInUp} 1.8s ease forwards;
  opacity: 0;

  &:hover {
    background: #4caf50; /* Hijau muda (Mint Green) */
    transform: translateY(-4px);
    box-shadow: 0 10px 28px rgba(76, 175, 80, 0.45);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 5px 15px rgba(76, 175, 80, 0.3);
  }

  @media (max-width: 768px) {
    font-size: 1.15rem;
    padding: 0.9rem 2.2rem;
    width: 100%;
    max-width: 300px;
  }
`;

const Home = () => {
  console.log("Image path:", smkawal);

  return (
    <>
      <GoogleFonts />
      <Section id="home">
        <Content>
          <MainTitle>Bersama Membentuk</MainTitle>
          <Subtitle>Sekolah Hijau</Subtitle>
          <CTAButton href="https://galeryeconity.netlify.app/">Lihat Kegiatan Kami</CTAButton>
        </Content>
      </Section>
    </>
  );
};

export default Home;